import React from 'react';
import PropTypes from 'prop-types';
import petCss from './Pet.module.css';

const Pet = ({ image, name, age, gender, color, breed, description, alt }) => (
  <>
    <h2>All about {name}</h2>
    <div className={petCss.wrapp}>
      <img src={image} alt={alt} className={petCss.image} />
      <div>
        <p className={petCss.text}>
          Age: <span className={petCss.data}>{age}</span>
        </p>
        <p className={petCss.text}>
          Geder: <span className={petCss.data}>{gender}</span>
        </p>
        <p className={petCss.text}>
          Color: <span className={petCss.data}>{color}</span>
        </p>
        <p className={petCss.text}>
          Breed: <span className={petCss.data}>{breed}</span>
        </p>
      </div>
    </div>
    <p>{description}</p>
  </>
);

Pet.defaultProps = {
  alt: `${Pet.name} photo`,
};

Pet.propTypes = {
  name: PropTypes.string.isRequired,
  age: PropTypes.number.isRequired,
  gender: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired,
  breed: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  alt: PropTypes.string,
};

export default Pet;
